
import { defineComponent, ref } from "vue";
import type { CategoryInfoDetails, ICM20200212_RES, ICM20200231_RES, ICM20200241_RES, ICM20200221_RES, } from "../../../shared/type/icm202002sc01.type";
import { modalController } from '@ionic/vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "icm202002sc01",

  components: {
    ModifyCard,
    BreadCrumb
  },
  
  data() {
    const categoryInfoDetails = ref<CategoryInfoDetails>({
      statusCode: "",
      categoryCode: "004",
      categoryName: "",
      categoryDesc: "",
      firstRegisterBranchCode: "",
      firstRegisterTellerID: "",
      firstRegisterTellerName: "",
      firstRegisterDate: "",
      firstRegisterTime: "",
      lastChangeBranchCode: "",
      lastChangeTellerID: "",
      lastChangeTellerName: "",
      lastChangeDate: "",
      lastChangeTime: "",
      deleteBranchCode: "",
      deleteTellerID: "",
      deleteTellerName: "",
      deleteDate: "",
      deleteTime: ""
    });
    const modifyCard = {
      registerTellerId: '',
      registerTellerName: '',
      registerDate: '',
      registerTime: '',
      updatedTellerId: '',
      updatedTellerName: '',
      updatedDate: '',
      updatedTime: '',
      deleteTellerId: '',
      deleteTellerName: '',
      deleteDate: '',
      deleteTime: '',
    }
    return {
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      modifyCard,
      categoryInfoDetails,
      isEditable: true,
      type: this.$route.query.type,
      isInValidInput: false,
      isFaqCategoryCode: true,
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'icm202002sc00.contents_policy_management', keyPath: '', type: 'label' },
        { value: 'icm202002sc00.faq', keyPath: '', type: 'label' },
        { value: 'pfm204001sc00.fee_by_customer_list', keyPath: 'ICM202002SC00', type: 'route' },
        { value: `${this.$route.query.type == 'register' ? 'icm202002sc01.faq_category_register' : 'icm202002sc01.faq_category_edit'}`, keyPath: '/ICM202002SC01', type: 'route' },
      ]
    };
  },
  async created() {
    if (this.approvalType === '') {
      if (this.$route.query.type === "edit") {
        this.getCategoryInfoDetail();
        this.isEditable = true;
        this.isFaqCategoryCode = false
      } else {
        this.isEditable = false;
      }
    } else {
      await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
        this.categoryInfoDetails.categoryCode = authDetailParsed.categoryCode
        this.categoryInfoDetails.categoryName = authDetailParsed.categoryName
        this.categoryInfoDetails.categoryDesc = authDetailParsed.categoryDesc
      })
    }
  },
  computed: {
    displayName(): string {
      return this.$route.query.type === "edit"
        ? this.$t("icm202002sc01.faq_category_edit")
        : this.$t("icm202002sc01.faq_category_register");
    },
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },
  methods: {
    // Function Detail : ICM20200212
    async getCategoryInfoDetail() {
      modalController.dismiss()
      const body = {
        categoryCode: this.$route.query.categoryCode
      };
      const responseData = (await this.$request.getRequest("ICM20200212", body, false)) as ICM20200212_RES;
      if (responseData.header.result) {
        this.categoryInfoDetails = responseData?.body;
        this.modifyCard = {
          registerTellerId: responseData?.body.firstRegisterTellerID,
          registerTellerName: responseData?.body.firstRegisterTellerName,
          registerDate: responseData?.body.firstRegisterDate,
          registerTime: responseData?.body.firstRegisterTime,
          updatedTellerId: this.categoryInfoDetails.statusCode === '09' ? "" : responseData?.body.lastChangeTellerID,
          updatedTellerName: this.categoryInfoDetails.statusCode === '09' ? "" : responseData?.body.lastChangeTellerName,
          updatedDate: this.categoryInfoDetails.statusCode === '09' ? "" : responseData?.body.lastChangeDate,
          updatedTime: this.categoryInfoDetails.statusCode === '09' ? "" : responseData?.body.lastChangeTime,
          deleteTellerId: this.categoryInfoDetails.statusCode === '09' ? responseData?.body.lastChangeTellerID : "",
          deleteTellerName: this.categoryInfoDetails.statusCode === '09' ? responseData?.body.lastChangeTellerName : "",
          deleteDate: this.categoryInfoDetails.statusCode === '09' ? responseData?.body.lastChangeDate : "",
          deleteTime: this.categoryInfoDetails.statusCode === '09' ? responseData?.body.lastChangeTime : "",
        }
      }
    },
    handleDelete() {
      this.$dialog.onOpen({
        content: this.$t("message.are_you_sure_you_want_to_delete"),
        dialogMode: 'Question',
        callback: () =>
          this.onConfirmDelete(),
      });
    },
    // Function Delete: ICM20200241
    async onConfirmDelete() {
      modalController.dismiss()
      const Body = {
        categoryCode: this.categoryInfoDetails.categoryCode,
      };
      const responseData = (await this.$request.getRequest(
        "ICM20200241",
        Body,
        false
      )) as ICM20200241_RES;
      if (responseData.header.result) {
        this.$router.push('/ICM202002SC00')
        this.$dialog.onOpen({
          content: this.$t("message.delete_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        });
      }
    },
    handleCancel() {
      this.$dialog.onOpen({
        content: this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data"),
        dialogMode: 'Question',
        callback: () =>
          this.onConfirmCancel()
      });
    },
    onConfirmCancel() {
      modalController.dismiss()
      if (this.type === "edit") {
        this.isEditable = true;
        this.isInValidInput = false;
        this.getCategoryInfoDetail();
      }
      else {
        this.$router.push('/ICM202002SC00')
      }
    },
    handleList() {
      this.$router.push('/ICM202002SC00')
    },
    handleEdit() {
      this.isEditable = false;
    },

    validateLength(event: any) {
      const inputValue = event.target.value;

      if (inputValue.length > 3) {
        return;
      }

      if (event.keyCode === 8 || event.key === 'Backspace' && event.keyCode !== 13) {
        return event.preventDefault()
      }
    },

    handleSave() {
      if (this.categoryInfoDetails.categoryName && this.categoryInfoDetails.categoryCode) {
        this.$dialog.onOpen({
          content: this.$t('message.would_you_like_to_save'),
          dialogMode: 'Question',
          callback: () => {
            if (this.type === "edit") {
              this.onConfirmUpdate();
            } else {
              this.onConfirmRegister();
            }
          },
        });
      } else {
        this.isInValidInput = true;
      }
    },
    // Function Update: ICM20200231
    async onConfirmUpdate() {
      modalController.dismiss()
      const Body = {
        categoryCode: this.categoryInfoDetails.categoryCode,
        categoryName: this.categoryInfoDetails.categoryName,
        categoryDesc: this.categoryInfoDetails.categoryDesc
      };
      const responseData = (await this.$request.getRequest("ICM20200231", Body, false)) as ICM20200231_RES;
      if (responseData.header.result) {
        this.isEditable = true;
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            this.getCategoryInfoDetail();
            modalController.dismiss()
          }
        });
      }
    },
    // Function Register : ICM20200221
    async onConfirmRegister() {
      modalController.dismiss()
      const Body = {
        categoryCode: this.categoryInfoDetails.categoryCode,
        categoryName: this.categoryInfoDetails.categoryName,
        categoryDesc: this.categoryInfoDetails.categoryDesc
      };
      console.log('create new FAQ :::', Body)
      const responseData = (await this.$request.getRequest("ICM20200221", Body, false)) as ICM20200221_RES;
      if (responseData.header.result) {
        this.$router.push('/ICM202002SC00')
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        });
      }
    },
  },
});
